.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.test{
  background-color: red;
}

.answered{
  background-color: green;
  color: white;
}
.some_answer{
  border: 1px solid green;
  color: green;
}
.question_post{
  text-align: left;
}
blockquote{
  margin-left: 30px!important;
  padding-left: 4px;
  border-left: 5px solid gray;
}
blockquote p{
  word-wrap: break-word;
}
pre{
  background-color: lightgray;
  padding: 10px;
  /* overflow-x: scroll!important;s */
}
.question-main{
  margin: 0 20px;
}
.profile-image{
  height: 50px;
  width: 50px;
}
.profile-info-question{
  display: flex;
  /* text-align: right; */
  /* position: absolute; */
  /* right: 20px; */
  /* margin: 10px 0; */
  
}
.profile-info-question p{
  margin-right: 5px;
  margin-left: auto;
}
.profile-info-question-2{
  display: flex;
  /* margin-left: auto; */
  margin-right: 0em;
}
.profile-info-question-2 p{
  margin: 0;
  padding: 0 5px;
}
.question-tags{
  background-color: lightskyblue;
  padding: 5px;
  margin: 0 5px;
}
.bronze{
  color: #cd7f32;
}
.silver{
  color: silver;
}
.gold{
  color: gold;
}
.tag{
  background-color: #6E85D6;
}
.tag-10{
  background-color: #4967D6;
}
.tag-20{
  background-color: #2C3F82;
}
.tag-30{
  background-color: #1535AD;
}
.tag-40{
  background-color: #270672;
}
.tag-50{
  background-color: #071D71;
}
.tags{
  color: white;
  padding: 5px;
  margin: 2px;
  border-radius: 5px;
  display: inline-block;
}
.privileges{
  background-color: yellowgreen;
  border-radius: 5px;
  display: inline-block;
  padding: 5px;
  margin: 2px;

}
.timeline{
  padding: 5px;
  background-color: lightgray;
  margin-bottom: 5px;
}
.timeline-type{
  display: flex;
  justify-content: space-around;
}
.timeline-title{
  background-color: darkgray;
  padding: 4px;
}
.associated-users{
  text-align: center;
}
.associated-user{
  display: inline-block;
  padding: 5px;
  width: 100px;
  margin-top: 0;
  margin-bottom: auto;
  height: auto;
  vertical-align: top;
}
.associated-user img{
  height: 80px;
  width: 80px;
}
.question-content{
  display: flex;
  border: 2px solid darkgray;
  margin: 5px;
  flex-wrap: wrap;
}
.question-content::after{
  content: '';
  width: 100%;
  order: 0;
}
.votes{
  display: flex;
  flex-wrap: wrap;
  align-content: space-around;
  width: 140px;
}
.votes div{
  min-width: 120px;
}
.home-tags{
  color: white;
  
}
.home-tags a{
  display: inline-block;
  background-color: gray;
  margin: 5px;
  padding: 2px;
  font-size: 12px;
}
.home-tags a:hover{
  background-color: dimgray;
  color: white;
}
.question-info{
  /* margin: auto; */
  width: calc(100% - 150px);
}
.author{
  background-color: darkorange;
  color: white;
}
.author:hover{
  color: white;
  text-decoration: none;
  background-color: teal;
}
.home-tags-tag{
  color: white;
}
.div-tag-author{
  display: flex;
  justify-content: space-between;
}
.answer-main{
  text-align: left;
  margin: 0 20px;
}
.answer{
  display: flex;
  border: 2px solid darkgray;
  padding: 5px;
  margin: 5px;
}
.answer-score{
  flex: none;
  width: 3em;
  text-align: center;
}
.answer-score p{
  font-size: 28px;
}
.answer-body{
  overflow-x: scroll;
}
.absolute-question{
  order: 1;
}
